

import Vue from 'vue'
import App from './App'
import router from './router.js'
import fb from "./assets/js/flexible.js"
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import $ from 'jquery'
import echarts from 'echarts'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Vuex from 'vuex'
import vuexI18n from 'vuex-i18n'


Vue.use(Vuex)
Vue.use(ElementUI);

let store = new Vuex.Store({
modules: {
i18n: vuexI18n.store
}
})
Vue.use(vuexI18n.plugin, store);

import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)

import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

Vue.use(DatetimePlugin)



import Hcss from "./assets/css/h-css.css"
import Public from "./assets/css/public.css"




Vue.config.productionTip = false



Vue.use(VueResource);
Vue.use(VueRouter);

function getCookie(name){
var reg = RegExp(name+'=([^;]+)');
var arr = document.cookie.match(reg);
if(arr){
return arr[1];
}else{
return '';
}
}
router.beforeEach((to, from, next) => {
next()

})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



