import Vue from 'vue'
import VueRouter from 'vue-router'



import HelloWorld from '@/components/HelloWorld'
import vision from '@/components/vision'

Vue.use(VueRouter)


const routes = [
    {
      path: '/',
      name: 'vision',
      component: vision
    },
    
]

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
	//base: "/h5test",
	routes
})
export default router




